export default function Card({item, isEnabled}) {
    

    const enabled = useMemo(()=>{isEnabled(item)},[item, isEnabled])
    const styles = useMemo(() => makeStyles(enabled),[enabled])

    return (
      <div style={styles.product_list_item}>
        <div style={styles.product}>
       <span style={styles.text}>
        {item.name}
        </span>
        <span style={styles.text}>
        {item.type}
        </span>
        <span style={styles.text}>
        {item.price}
        </span>
      </div>
      
      </div>
    );
  }